import * as XmlGenerator from 'xml';
import { merge } from 'lodash';

import createRelationshipsXml, { IRelationship } from './createRelationshipsXml';
import createComponentXml from './createComponentXml';
import IFeature from '../models/packageDefinition/IFeature';
import IComponent from '../models/packageDefinition/IComponent';
import IResources from '../models/packageXml/IResources';
import IFeatureXml from '../models/packageXml/IFeatureXml';
import IXml from '../models/packageXml/IXml';
import IOpenDocumentConventionXml from '../models/packageXml/IOpenDocumentConventionXml';
import serializationOptions from './serializationOptions';

export default function createFeatureXml(feature: IFeature, resources: IResources): IFeatureXml {
  feature = merge({
    version: '1.0.0.0'
  }, feature) as IFeature;

  const featureXmlFile: string = `feature_${feature.id}.xml`;

  const featureXml: IOpenDocumentConventionXml = {
    filename: featureXmlFile,  // ensure this is unique
    /* tslint:disable:no-null-keyword */
    relationships: null,
    /* tslint:enable:no-null-keyword */
    xml: XmlGenerator({
      Feature: [
        {
          _attr: {
            xmlns: 'http://schemas.microsoft.com/sharepoint/',
            Title: feature.title,
            Description: feature.description,
            Id: feature.id,
            Version: feature.version,
            Scope: 'Web', // @todo change to Site scope
            Hidden: 'FALSE'
          }
        }
      ]
    }, serializationOptions)
  };

  const configXml: IXml = {
    filename: `${featureXmlFile}.config.xml`,
    xml: XmlGenerator({
      AppPartConfig: [
        {
          _attr: {
            xmlns: 'http://schemas.microsoft.com/sharepoint/2012/app/partconfiguration'
          }
        },
        {
          Id: feature.id // @todo needs to be a unique id, different from feature.id
        }
      ]
    }, serializationOptions)
  };

  const componentsXml: IXml[] = feature.components.map((component: IComponent) => {
    return createComponentXml(component, feature.id);
  });

  const relationshipsXml: IXml = createRelationshipsXml(featureXmlFile,
        [
          {
            type: 'partconfiguration',
            target: configXml.filename
          } as IRelationship
        ].concat(
          componentsXml.map((component: IXml): IRelationship => {
            return {
              type: 'feature-elementmanifest',
              target: component.filename
            };
          })
        ).concat( (resources.localizedResourcePaths.length > 0) ?
          resources.localizedResourcePaths.map((filename: string) => {
            return {
              type: 'content-resource',
              target: filename
            };
          })
          : []
        ).concat( (resources.defaultResourcePath)
          ? {
              type: 'content-defaultresource',
              target: resources.defaultResourcePath
          }
          : []
        )
  );

  return {
    filename: featureXml.filename,
    xml: featureXml.xml,
    relationships: relationshipsXml,
    config: configXml,
    components: componentsXml
  };
}
